import React, { Component } from 'react';
import Footer from './Footer';
import Particle from './Particle';
import { Grid, Typography } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import ProfilePic from '../images/swaroop.jpg';

const style = {
  header: {
    position: "absolute",
    textAlign: "center",
    top: "12%",
    left: "20%"
  },
  Typography: {
    marginTop: 120
  }
}

const theme = createMuiTheme({
  palette: {
    primary: {
        main: "#ffffff",
    },
    secondary: {
      main: "#FF5722",
    },
  },
});

class About extends Component {
  render() {
    return (
      <div>
        <MuiThemeProvider theme={theme}>
          <div style={ style.header }>
            <Grid container>
              <Grid item sm>
                    <Typography align="left" color = "primary" variant="display1" style={style.Typography}>
                      A lover of technology
                    </Typography>
                    <Typography align="justify" color = "primary" variant="subheading" style={{ marginTop: 20 }}>
                    I am an experienced Software Engineer with a demonstrated history of working in the information technology and services industry. Strong engineering professional skilled in Angular, React, C#, Java, JSP/Servlets, Asp.net, Node Js, Express Js, Pega DSM, and Machine Learning.
                    </Typography>
                </Grid>
              <Grid item sm>
                  <img src={ ProfilePic } height="10%" style={{ marginTop:"30px" }}/>
                </Grid>
              </Grid>
              </div>
            </MuiThemeProvider>
            <Particle/>
        <Footer/>
      </div>
    );
  }
}

export default About;



