import React, { Component } from 'react';
import Footer from './Footer';
import { Grid, Paper, Typography, Button } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import Particle from './Particle';
import Resume from '../Assets/ResumeUpdated.pdf';

export const style = {
  Paper: {
    padding: 20,
    marginTop: 15,
    marginBotton: 10
  },
  Typography: {
    padding: 140
  },
  header: {
    position: "absolute",
    textAlign: "center",
    top: "12%",
    left: "20%"
  }
}

const theme = createMuiTheme({
  palette: {
    primary: {
        main: "#ffffff",
    },
    secondary: {
      main: "#FF5722",
    },
  },
});

class Home extends Component {
  render() {
    return (
      <div>
        <MuiThemeProvider theme={theme}>
          <div style={ style.header }>
            <Grid container>
              <Grid item sm>
                  <div style={style.Typography}>
                    <Typography align="center" color = "primary" variant="display2" style={{margin: 5}}>
                      Sai Krishna Swaroop
                    </Typography>
                    <Typography align="center" color = "primary" variant="title" style={{margin: 7}}>
                      Software Developer
                    </Typography>
                    <Button variant="contained" color = "secondary" style={{margin: 8}} href={ Resume } target="_blank">
                      Download Resume
                    </Button>
                  </div>
                </Grid>
              </Grid>
              </div>
            </MuiThemeProvider>
            <Particle/>
        <Footer/>
      </div>
    );
  }
}

export default Home;



