import React, { Component } from 'react';
import Footer from './Footer';
import Particle from './Particle';
import { Grid, Typography, LinearProgress, List, ListItem, ListItemText, Divider } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import MyTheme from './MyTheme';


const style = {
  header: {
    position: "absolute",
    textAlign: "center",
    top: "12%",
    left: "20%"
  },
  Typography: {
    marginTop: 50
  }
}

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#1769aa"
  },
  secondary: {
    main: "#FF5722"
  },
  },
});

class About extends Component {
  render() {
    return (
      <div>
        <MuiThemeProvider theme={theme}>
          <div style={ style.header }>
            <Grid container>
              <Grid item sm style={{ backgroundColor:"white", margin: "60px", width: "850px" }}>
                  <List component="nav">
                    <ListItem button> 
                      <ListItemText primary="Pega" />
                      <span style={{color:"#1769aa", margin:5}}>{"90%"}</span><LinearProgress variant="determinate" value={90} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider />
                    <ListItem button>
                      <ListItemText primary="Angular" />
                      <span style={{color:"#1769aa", margin:5}}>{"70%"}</span><LinearProgress variant="determinate" value={70} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider />
                    <ListItem button>
                      <ListItemText primary="React" />
                      <span style={{color:"#1769aa", margin:5}}>{"80%"}</span><LinearProgress variant="determinate" value={80} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider/>
                    <ListItem button>
                      <ListItemText primary="React Native" />
                      <span style={{color:"#1769aa", margin:5}}>{"60%"}</span><LinearProgress variant="determinate" value={60} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider/>
                    <ListItem button>
                      <ListItemText primary="Node JS" />
                      <span style={{color:"#1769aa", margin:5}}>{"50%"}</span><LinearProgress variant="determinate" value={50} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider/>
                    <ListItem button>
                      <ListItemText primary="JSP/Servlets" />
                      <span style={{color:"#1769aa", margin:5}}>{"60%"}</span><LinearProgress variant="determinate" value={60} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                    <Divider/>
                    <ListItem button>
                      <ListItemText primary="C#" />
                      <span style={{color:"#1769aa", margin:5}}>{"90%"}</span><LinearProgress variant="determinate" value={90} style={ MyTheme.palette.companyBlue }/>
                    </ListItem>
                  </List>
                </Grid>
              </Grid>
              </div>
            </MuiThemeProvider>
            <Particle/>
        <Footer/>
      </div>
    );
  }
}

export default About;



