import React, { Component } from 'react';
import Particles from 'react-particles-js';

const particleOpt = {
    particles: {
      number: {
        value: 25,
        density: {
          enable: "true",
          value_area: 900
        }
      },
      shape: {
        type: "circle",
        stroke: {
          width: 4,
          color: "#ffffff"
        }
      }
    },
    interactivity:{
        events: {
            onhover: {
                enable: "true",
                mode: "repulse"
            }
        }
    }
}

class Particle extends Component {
  render() {
    return (
        <div>
          <Particles
            params={particleOpt}
          />
        </div>
    );
  }
}

export default Particle;



