import { createMuiTheme } from '@material-ui/core/styles';
import purple from '@material-ui/core/colors/purple';

export default createMuiTheme({
    palette: {
        primary: { // works
          main: '#165788',
          contrastText: '#fff',
          height: "30px",
          margin: "auto", 
          width: "300px",
        },
        secondary: { // works
          main: '#69BE28',
          contrastText: '#65CFE9',
          height: "30px",
          margin: "auto", 
          width: "300px",
        },
        companyBlue: {
            backgroundColor: '#65CFE9',
            color: '#fff',
            margin: "auto", 
            width: "300px",
            height: "30px",
        },
        companyRed: { 
            backgroundColor: '#E44D69',
            color: '#E44D69',
            margin: "auto", 
            width: "300px",
            height: "30px",
        },
        accent: { 
            backgroundColor: purple[500], 
            color: '#000',
            margin: "auto", 
            width: "300px",
            height: "30px",
        },
    },
});


