import React, { Component } from 'react';
import { AppBar, Toolbar, Typography, Button, Menu, MenuItem } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import Main from './Main';


const theme = createMuiTheme({
  palette: {
    primary: {
        main: "#0d0541",
    }
  },
});

const style = {
    fontFamily: "Montserrat Alternates sans-serif",
    fontSize: 30,
    fontWeight: 200,
    marginLeft: 50
}

class Navbar extends Component {
  render() {
    return (
      <div>
        <MuiThemeProvider theme={theme}>
            <AppBar position="static" color="primary">
                <Toolbar>
                    <Typography variant="title" color="inherit" style={ style }>
                        Sai Krishna Swaroop
                    </Typography>
                    <Button href = "/" color="inherit" style={{ marginLeft: 400 }}>Home</Button>
                    <Button href ="/about" color="inherit" >About</Button>
                    <Button href ="/skills" color="inherit" >Skills</Button>
                    <Button href ="/experience" color="inherit" >Experience</Button>
                    <Button href ="/contact" color="inherit" >Contact</Button>
                </Toolbar>
            </AppBar>
        </MuiThemeProvider>
        <Main/>
      </div>
    );
  }
}

export default Navbar;



